/* Front Page | Middle section */
#middle{
  width: 34%;

  display: flex;
  flex-direction: column;
  justify-content:center;
}

#my_img{
  border-radius: 0% 10%;
  width: 100%;
  border: 2px solid transparent;
  animation-name: shadow;
  animation-duration: 8s;
  animation-iteration-count: infinite;
  box-shadow: -5px 0px 10px 1px #0077b5;
}

@keyframes shadow {
  0%   {box-shadow: -5px 0px 10px 1px #0077b5;
  border-top: 2px solid transparent;}
  12.5% {box-shadow: -2.5px 2.5px 15px 1px #00afb5;}
  25%  {box-shadow: 0px 5px 10px 1px #00afb5;
    border-radius: 5% 10% 5%;
    border-left: 2px solid transparent;}
  37.5%{box-shadow: 2.5px 2.5px 15px 1px #00afb5;}
  50%  {box-shadow: 5px 0px 10px 1px #6edabf;
  border-radius: 10% 0% 10%;
  border-bottom: 2px solid transparent;}
  62.5%{box-shadow: 2.5px -2.5px 13px 1px #22878a;}
  75% {box-shadow: 0px -5px 10px 1px #00afb5;
    border-radius: 5% 10% 5%;
    border-right: 2px solid transparent;}
  87.5%{box-shadow: -2.5px -2.5px 12px 1px #00afb5;}
  100% {box-shadow: -5px 0px 10px 1px #0077b5;}
}

@keyframes border {
  0%   {border-radius: 15%}
  25%  {border-radius: 0%;}
  50%  {border-radius: 7.5%;}
  100% {border-radius: 15%;}
}


