/* Personalizar icons no Contacts */
#socials ul {
  display: flex;
  margin-top: 10%;
  margin-left: 0px !important;
  margin-right: 0px !important;
  padding: 0 !important;
  
}
#socials ul li {
  position: relative;
  list-style: none;
  cursor: pointer;
  padding: 1vw;
  
}
#socials ul li a {
  text-decoration: none;
}
#socials ul li a .fa-brands, .fa-solid, .fa-map-marker-alt {
  font-size: 3vw;
  color: #222;
}
#socials ul li a::before {
  font-family: "FontAwesome";
  position: absolute;
  font-size: 3vw;
  height: 0;
  overflow: hidden;
  transition: 0.5s ease-in-out;
}

#socials ul li:nth-child(1) a::before {
  font-size: 3vw;
  content: "\f08c";
  color: #0077b5;
  border-bottom: 4px solid #0077b5;
  top: 0.2vw;
}

#socials ul li:nth-child(2) a::before {
  font-size: 3vw;
  content: "\f092";
  color: white;
  border-bottom: 4px solid white;
  top: 0.2vw;
}

#socials ul li:nth-child(3) a::before {
  font-size: 3vw;
  content: "\f16d";
  background-image: linear-gradient(45deg, 
    #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,
    #bc1888 100%);
  -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
  border-bottom: 4px solid #dc2743;
  top: 0.2vw;
  
}

#socials ul li:hover a::before {
  height: 9rem;
}



